  /*---------------------------------------------*/
  .how-bor1 {
    border-bottom: 1px solid rgba(255,255,255,0.1);
  }
  
  .how-bor1:last-child {
    border: none;
  }
  
  /*---------------------------------------------*/
  .how-bor2 {
    border-bottom: 1px solid #f0f0f0;
  }
  
  .how-bor2:last-child {
    border: none;
  }
  
  /*---------------------------------------------*/
  .how-bor3 {
    border-bottom: 1px solid #e6e6e6;
  }
  
  .how-bor3:first-child {
    border-top: 1px solid #e6e6e6;
  }
  
  /*---------------------------------------------*/
  .how-txt1 {
    white-space: normal;
    display: box;
    display:-moz-box;
    display:-webkit-box;
    -webkit-line-clamp: 2;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  
  /*---------------------------------------------*/
  .how-txt2 {
    white-space: normal;
    display: box;
    display:-moz-box;
    display:-webkit-box;
    -webkit-line-clamp: 1;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  
  /*---------------------------------------------*/
  .how-txt3 {
    white-space: normal;
    overflow: hidden;
  }
  
  /*---------------------------------------------*/
  .how-overlay1 {
    position: relative;
    z-index: 1;
  }
  
  .how-overlay1::before {
    content: "";
    display: block;
    position: absolute;
    z-index: -1;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    opacity: 0.5;
    background: rgba(0,0,0,0.5);
    background: -webkit-gradient(linear, left bottom, left top, from(rgba(0,0,0,1)), to(rgba(0,0,0,0)));
    background: linear-gradient(bottom, rgba(0,0,0,1), rgba(0,0,0,0));
  }
  
  /*---------------------------------------------*/
  .how1 {
    position: relative;
  }
  
  .how1-child1 {
    position: absolute;
    z-index: 2;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    opacity: 0.5;
    background: rgba(0,0,0,0.5);
    background: -webkit-gradient(linear, left bottom, left top, from(rgba(0,0,0,1)), to(rgba(0,0,0,0)));
    background: linear-gradient(bottom, rgba(0,0,0,1), rgba(0,0,0,0));
  }
  
  .how1:hover .how1-child1 {
    opacity: 0.3;
  }
  
  .how1-child2 {
    z-index: 3;
  }
  
  /*---------------------------------------------*/
  .how2 {
    height: 50px;
    border: 1px solid #e6e6e6;
    padding: 0 18px;
    position: relative;
  }
  
  .how2::before {
    content: "";
    display: block;
    position: absolute;
    width: 5px;
    height: calc(100% + 2px);
    top: -1px;
    left: -1px;
  }
  
  .how2-cl1::before {
    background-color: #e71d69;
  }
  
  .how2-cl2::before {
    background-color: #15a752;
  }
  
  .how2-cl3::before {
    background-color: #e3724a;
  }
  
  .how2-cl4::before {
    background-color: #333;
  }
  
  .how2-cl5::before {
    background-color: #00b5e9;
  }
  
  .how2-cl6::before {
