  background: #d82a4e;
  margin-bottom: 10px;
  margin-right: 6px;
  font-weight: 500;
}

.site-pagination {
  padding-top: 30px;
  padding-bottom: 30px;
}

.site-pagination a,
.site-pagination span {
  font-size: 16px;
  color: #474747;
  font-weight: 600;
}

.site-pagination span.active {
  color: #d82a4e;
}

/*------------------
  Contact Page
---------------------*/

.contact-form-warp {
  background: #d82a4e;
  padding: 45px 47px;
}

.contact-form-warp .section-title {
  padding: 0 15px;
  margin-bottom: 50px;
}

.contact-form-warp .contact-form {
  padding: 69px 40px 47px;
}

.contact-info-area {
  padding-top: 60px;
}

.contact-info-area .section-title {
  margin-bottom: 80px;
}

.contact-info-area .phone-number {
  margin-bottom: 100px;
}

.contact-info-area .phone-number span {
  font-size: 18px;
  font-weight: 700;
  color: #d82a4e;
  display: block;
  margin-bottom: 5px;
}

.contact-info-area .phone-number h2 {
  color: #646464;
  display: inline-block;
  position: relative;
  padding: 0 0 10px;
  padding-right: 12px;
}

.contact-info-area .phone-number h2:after {
  position: absolute;
  content: "";
  width: 100%;
  height: 3px;
  left: 0;
  bottom: 0;
  background: #d82a4e;
}

.contact-info-area .contact-list {
  list-style: none;
  padding-left: 50px;
}

.contact-info-area .contact-list li {
  margin-bottom: 27px;
  position: relative;
  padding-left: 10px;
  font-size: 14px;
  font-weight: 500;
  color: #878787;
}

.contact-info-area .contact-list li:last-child {
  margin-bottom: 0;
}

.contact-info-area .contact-list li:after {
  position: absolute;
  content: "";
  width: 8px;
  height: 8px;
  left: -5px;
  top: 6px;
  border-radius: 50%;
  background: #d82a4e;
}

.contact-info-area .social-links {
  padding-top: 60px;
  padding-left: 50px;
}

.contact-info-area .social-links a {
  font-size: 14px;
  color: #707070;
  margin-right: 30px;
}

.contact-info-area .social-links a:last-child {
  margin-right: 0;
}

#map-canvas {
  height: 484px;
  margin-top: 58px;
  background: #ddd;
}

/*------------------
  Elements Page
---------------------*/

.element {
  margin-bottom: 110px;
}

.element .e-title {
  font-size: 48px;
  color: #474747;
  margin-bottom: 110px;
}

/* Accordion */

.accordion-area .panel {
  margin-bottom: 18px;
}

.accordion-area .panel-link:after {
  content: "+";
  position: absolute;
  font-size: 18px;
  font-weight: 700;
  right: 25px;
  top: 50%;
  color: #000;
  line-height: 16px;
  margin-top: -8px;
  margin-left: -4px;
}

.accordion-area .panel-header .panel-link.active {
  background: #d82a4e;
  color: #fff;
}

.accordion-area .panel-header .panel-link.collapsed {
  background: #f8fafc;
  color: #1d1d1d;
}

.accordion-area .panel-header .panel-link.collapsed:after {
  content: "+";
  color: #1d1d1d;
}

.accordion-area .panel-link.active:after {
  content: "-";
  margin-top: -10px;
  color: #fff;
}

.accordion-area .panel-link {
  text-align: left;
  background-color: transparent;
  border: none;
  position: relative;
  width: 100%;
  padding: 15px 40px;
  padding-left: 30px;
  font-size: 16px;
  font-weight: 600;
  background: #e4edef;
  color: #1f1f1f;
}

.accordion-area .panel-body p {
  margin-bottom: 0;
  padding-top: 35px;
}

.accordion-area .panel-body {
  padding: 0 5px;
}

/* tab */

.tab-element .nav-tabs {
  border-bottom: none;
}

.tab-element .nav-tabs .nav-link {
  border-radius: 0;
  font-size: 16px;
  font-weight: 600;
  color: #1f1f1f;
  border: none;
  padding: 17px 23px;
  margin-right: 8px;
  background: #e4edef;
  margin-bottom: 10px;
}

.tab-element .nav-tabs .nav-item:last-child .nav-link {
  margin-right: 0;
}

.tab-element .nav-tabs .nav-link.active {
  background: #d82a4e;
  color: #fff;
}

.tab-element .nav-tabs .nav-link:focus,
.tab-element .nav-tabs .nav-link:hover {
  border: none;
}

.tab-element .tab-pane p {
  padding-top: 35px;
}

/* Circle progress */

.circle-progress {
  text-align: center;
  display: block;
  position: relative;
  margin-bottom: 30px;
}

.circle-progress canvas {
  -webkit-transform: rotate(90deg);
  -ms-transform: rotate(90deg);
  transform: rotate(90deg);
}

.circle-progress .progress-info {
  position: absolute;
  width: 100%;
  top: 50%;
  margin-top: -45px;
}

.circle-progress .progress-info h2 {
  font-weight: 700;
  margin-bottom: 5px;
}

.circle-progress .progress-info p {
  font-size: 14px;
  margin-bottom: 0;
}

.fact-item h2 {
  font-size: 60px;
  color: #d82a4e;
}

.icon-box h5 {
  margin-bottom: 20px;
}

.icon-box h5 span {
  font-size: 60px;
  color: #d82a4e;
  margin-right: 15px;
}

